// Libraries
import React from 'react';
import {Link} from 'react-router-dom'

//Assets
import catalogIcon from '../../../assets/images/png/navbar/catalogIcon.png'
import searchIcon from '../../../assets/images/png/navbar/searchIcon.png'


const Navbar = () => {
  return (
  <div className="navbar">
    <div className="navbar__wrapper">
      <div className="navbar__el"><img className="navbar__el_icon" src={catalogIcon} alt=""/><Link className="navbar__link" to="/catalog"></Link></div>
      <div className="navbar__el"><Link className="navbar__link" to="/proizvodstvo"></Link></div>
      <div className="navbar__el"><Link className="navbar__link" to="/plity_dorozhnye-pdn"> </Link></div>
      <div className="navbar__el"><Link className="navbar__link" to="/svai-zabivnye-zhelezobetonnye"></Link></div>
      <div className="navbar__el"><Link className="navbar__link" to="/plity-perekrytij-pb-70"> </Link></div>
      <div className="navbar__el"><Link className="navbar__link" to="/novosti">  </Link></div>
      <div className="navbar__el"><Link className="navbar__link" to="/kontakty"></Link></div>
      <div className="navbar__search">
        <input className="navbar__search_input" type="text" placeholder="  "></input>
        <button className="navbar__search_button"><img className="navbar__search_icon" src={searchIcon} alt=""/></button>
      </div>
    </div>
  </div>
    );
};

export default Navbar;
